/* Global styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.contact-information{
  display: flex;
  gap: 10px;
  padding: 10px;
}

.links>a{

  margin: 10px;
}

.logo > img{
  width: 70px;
  background-color: white;
  border-radius: 50%;
}

.contact-container{
  display: flex;
  background-color: rgb(20, 2, 2);
  color: white;
  justify-content: space-between;
  align-items: center;
}

/* Header styles */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #333;
  color: white;
}

.logo {
  flex-grow: 1;
  text-align: center;
}

.menu-button {
  background-color: #333;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
}

/* Dropdown menu styles */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 100%;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  background-color: #f1f1f1;
}

.dropdown:hover .dropdown-content {
  display: block;
}


.timing{
  height: 100vh;
  display: flex;
}

.brown{
  background-color: rgb(20, 2, 2);
  color: white;
}

.timing-info{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50%;
}
/* Main section styles */
.main {
  height: 100vh;
  background-image: url('../public/images/main-background.jpg');
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
}

.half-height {
  height: 50%;
  width: 100%;
  display: flex;
  align-items: center;
  color: white;
  text-align: center;
}

.content {
  font-size: 24px;
}

/* Footer styles */
.footer {
  background-color: rgb(20, 2, 2);
  color: white;
  padding: 20px;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.div-image1{
  background-image: url('../public/images/1.jpg');
  background-size: contain;
  width: 50%;
  margin: 10%;
  
}

.div-image2{
  background-image: url('../public/images/2.jpg');
  background-size: cover;
  width: 50%;
  margin: 10%;
}

.footer > div {
  width: 50%; /* Adjust the width as needed */
  margin-bottom: 20px;
}

.contact-info, .hours, .follow, .address {
  display: flex;
  flex-direction: column;
}

.follow a {
  color: white;
  text-decoration: none;
  margin: 0 5px;
}

a {
  text-decoration: none;
  color: white;
}

/* Media queries for responsiveness */
@media (max-width: 768px) {
  /* Header styles */
  .header {
    flex-direction: column;
    align-items: center;
  }

  .menu-button {
    margin-top: 10px;
  }

  /* Footer styles */
  .footer > div {
    width: 100%; /* Set the width to 100% for full width on smaller screens */
  }

  .footer {
    flex-direction: column; /* Stack footer items vertically on smaller screens */
  }
}
